import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

//registro semana
import { semana_A } from '../modelo_A/semana_A';

//calendario
import { CalendarComponentOptions } from 'ion2-calendar'




@Component({
  selector: 'app-menu-altiplano',
  templateUrl: './menu-altiplano.page.html',
  styleUrls: ['./menu-altiplano.page.scss'],
})
export class MenuAltiplanoPage implements OnInit {

  //calendario
  dateMulti: string[];
  type: 'string';

  optionsMulti: CalendarComponentOptions = {
    pickMode: 'multi'
  };

   //del registro de la semana

   objeto:semana_A = {numsemana:"", descripcion:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:"" }
  
   semanas: semana_A[] = [];
   url2 = 'http://localhost/proG/server_api/menu_semanal_A.php';
   url = 'http://localhost/proG/server_api/crear_semana_A.php';
  



  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute 
  
    ) {
    this.listarSemanas();
  }

  getSemanas(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarSemanas(){
    this.semanas = [];
    this.getSemanas().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.semanas.push(res[i]);
      }
    })
  }

  agregarSemana(obj :semana_A): Observable<semana_A>{
    return this.http.post<semana_A>(this.url, JSON.stringify(obj))
  }

  async agregar() {
    this.agregarSemana(this.objeto)
    .subscribe((resp) =>{
      this.objeto.numsemana='';
      this.objeto.descripcion='';
      this.objeto.lunes='';
      this.objeto.martes='';
      this.objeto.miercoles='';
      this.objeto.jueves='';
      this.objeto.viernes='';
      this.objeto.sabado='';
      this.objeto.domingo='';
      this.listarSemanas();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }


  ngOnInit() {
  }

   //calendario
   onChange($event) {
    console.log($event);
  }



  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_MSA',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarSemanas();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }


  abrirCrud(a){
    this.router.navigate(['/mod-menua/'+a]);
  }

}
